
import Selector from 'vux/src/components/selector/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XNumber from 'vux/src/components/x-number/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import Clipboard from 'clipboard';
import CountDown from "./count-down.vue";
import ContentWrap from "./content-wrap.vue";
import Data from "./data.js";
import Scroll from "./scroll.vue";

var reg = /\?id=(\w+)&?/;
reg.test(location.href);
export default {
name: "page2-info",
components: {
Selector,
XDialog,
Alert,
Loading,
Group,
Cell,
Loading,
Datetime,
XInput,
XNumber,
XSwitch,
XTextarea,
Scroll,
ContentWrap,
CountDown
},
data() {
return {
address: '',
shown: false,
pshown: false,
alertShow: false,
alertContent: "",
loading: false,
tousuDialog: false,
supportDialog: false,
appDialog: false,
home: true,
ispreview: false,
users: [],
user_id: '',
shareId: '',
params: {},
orderList: [],
packageList: [],
order: [],
formD: Data,
isLoading: true,
name: "",
tel: "",
type: '',
options: [{
key: 0,
value: ""
}, {
key: 1,
value: ""
}],
list: [],
musicUrl: ""
};
},
props: {
form: {
type: Object
}
},
mounted() {
this.query();
},
methods: {
addActive(){
if(this.ispreview){
return;
}
if(!this.name || !this.tel){

return  this.$vux.toast.text('', 'top')
}
this.$http.post("https://wx.sharkmeida.cn/gather/makeLike", {
activityId: this.params.id,
userId: this.user_id,
prizeNum: this.formD.targetNum.toString(),
mobile: this.tel,
userName: this.name,
prizeTime: this.formD.restrictTime
}).then(({
data
}) => {
if(data.code == 0){
this.$vux.alert.show({
title: '',
content: ''
})
this.shareId = data.result.data.id
this.shown = false;
}else{
this.$vux.alert.show({
title: '',
content: data.msg
})
}
});
},
addZan(){
if(this.ispreview){
return;
}
if(!!this.params.shareId && !!this.shareId){

return  this.$vux.toast.text('')
}
this.$http.post("https://wx.sharkmeida.cn/gather/like", {
activityId: this.params.id,
id: this.params.shareId || this.shareId,
likes: this.user_id
}).then(({
data
}) => {
if(data.code == 0){
this.$vux.alert.show({
title: '',
content: ''
})
}else{
this.$vux.alert.show({
title: '',
content: data.msg
})
}
});
},
mapInit() {
var self = this;
if (self.formD.latitude != '' && self.formD.longitude != '') {
var map = new qq.maps.Map(document.getElementById('showPosition'), {
center: new qq.maps.LatLng(39.916527,116.397128),
zoom: 13,
panControl: false,
zoomControl: false,
scaleControl: false,
});

var marker = new qq.maps.Marker({
position: new qq.maps.LatLng(self.formD.latitude, self.formD.longitude),
map: map
});
var geocoder = new qq.maps.Geocoder({
complete: function (result) {
map.setCenter(result.detail.location);
self.address = result.detail.address
}
});
geocoder.getAddress(new qq.maps.LatLng(self.formD.latitude, self.formD.longitude))
}

},
close() {
this.supportDialog = false;
this.appDialog = false;
},
support() {
this.supportDialog = true;
},
goapp() {
this.appDialog = true;
},
phone() {
this.pshown = true;
},
showntousu() {
this.tousuDialog = true;
},
tousu() {
this.$vux.toast.text("", 'top')
this.tousuDialog = false;
},
copy() {
var clipboard = new Clipboard('#copyBtn');
clipboard.on('success', (e) => {
this.$vux.toast.text("", 'top')
e.clearSelection();
});
},
showdialoge() {
this.name = '';
this.type = "";
this.tel = "";
},
hidedailog() {
this.shown = false;
this.pshown = false;
},
stop() {
var audio = document.getElementById("audio2");
if (audio == null) return;
audio.pause();
},
play(item) {
var audio = document.getElementById("audio2");
if (audio == null) return;
if (this.formD.autoPlayMusic) {
let data = JSON.parse(this.formD.music);
audio.src = data.path;
audio.pause();
audio.load();
audio.play();
}
},

query() {
if (!!this.form) {
this.formD = this.form;
this.ispreview = true;
this.$refs['bg-main'].style.background = !this.formD.bgImage ? '#192029' : `url(${this.formD.bgImage})`
this.mapInit();
return;
}
var params = {};
location.search.split("?")[1].split('&').forEach((e) => {
let key = e.split('=')[0]
params[key] = e.split('=')[1]
})
this.params = params;
if (!params.id) {
return console.log('id is null')
}
if (!!params.code) {
this.$http.get('https://wx.sharkmeida.cn/api/wxpay/getUserInfo?code=' + params.code + "&state=" + params.state).then(({
data: res
}) => {
if (res.code != '0000') {
console.log('')
} else {
console.log("", res.result.data.user.userId)
this.user_id = res.result.data.user.userId;
}
})
}
this.$http.post("https://wx.sharkmeida.cn/gather/queryLike", {activityId: params.id}).then(({
data
}) => {
if(data.code == 0){
this.list = data.result.data
}
});
this.loading = true
this.$http.get("https://wx.sharkmeida.cn/gather/info/" + params.id).then(({
data
}) => {
if (data.code == "0") {
this.formD = data.gather;
this.$refs['bg-main'].style.background = !this.formD.bgImage ? '#192029' : `url(${this.formD.bgImage})`
this.mapInit();
var fore = data.gather;
document.title = fore.activityName;
var thumbnail = data.gather.thumbnail;
var currentUrl = encodeURIComponent(location.href.split('#')[0])
this.$http
.post(
"https://wx.sharkmeida.cn/api/wxpay/initwxjs?url=" + currentUrl
)
.then((data, status) => {
this.loading = false
wx.config({
debug: false, // ,apialertpclogpc
appId: data.data.result.data.appId, // 
timestamp: data.data.result.data.timestamp, // 
nonceStr: data.data.result.data.nonceStr, // 
signature: data.data.result.data.signature, // s
jsApiList: ["onMenuShareTimeline", "onMenuShareAppMessage", 'chooseWXPay'] 
});

wx.ready(function () {


wx.error(function (res) {

console.log(res);
});

wx.checkJsApi({
jsApiList: ["onMenuShareTimeline", "onMenuShareAppMessage", 'chooseWXPay'], // JSJS2,
success: function (res) {



}
});
var shareParam = {
title: fore.activityName, // 
desc: fore.priceDescription, // 
link: "https://wx.sharkmeida.cn/dist/redirect.html?id=" + params.id + "&userid=" + this.user_id + "&shareId=" + this.shareId + "&hash=2pageInfo", // JS
imgUrl: thumbnail, // 


trigger: function (res) {
console.log("");
},
success: function (res) {
console.log("");
},
cancel: function (res) {
console.log("");
},
fail: function (res) {}
};
wx.onMenuShareTimeline(shareParam);
wx.onMenuShareAppMessage(shareParam);
});
});
}
});
}
}
};



